.checkbox {
    appearance: none;
    width: 15px;
    height: 15px;
    border: 1px black solid;
    position: relative;
    color: dodgerblue;
}

.checkbox:checked:after{
    position: absolute;
    appearance: none;
    content: "X";
    font-weight: bold;
    font-size: 20px;
    transform: translateY(-15%) translateX(2%);

}




